<template>
    <section class="whole-last-link-container relative">
        <section class="last-link-container relative flex">
            <h3>Your last shortened link</h3>
            <div class="result flex relative">
                <p class="lastUserLink" v-html="link1"></p>
                <hr>
                <div class="link-button-container relative flex">
                    <input type="text" class="relative lastShortLink" :value="link2" readonly/>
                    <a class="square lastLinkCopyBtn" @click="copyLink" v-html="copyBtn"></a>
                </div>
            </div>
        </section>
    </section>
</template>

<script>
export default {
    name:'lastLink',
    data(){
        return{
            link1:localStorage.getItem('userLink'),
            link2:localStorage.getItem('shortLink'),
            copyBtn:'Copy',
        }
    },
    methods:{
        copyLink(){
            //copiar el link al portapapepels
            let linkToCopy = document.querySelector(".lastShortLink");
            let copyBtn = document.querySelector('.lastLinkCopyBtn')

            linkToCopy.setAttribute('type', 'text')
            linkToCopy.select()
            const copiar = document.execCommand('copy');  

            //estilear bot√≥n de copiar
            copyBtn.style.background = '#3A3053'
            this.copyBtn = 'Copied!'
        }
    }
}
</script>

<style scoped lang="scss">
@import '../../variables/variables.scss';

.whole-last-link-container{

    .last-link-container{
        width:85%;
        left:50%;
        align-items: flex-start;
        transform:translateX(-50%);
        flex-direction: column;
        max-width: 55rem;

        h3{
            margin:2rem 0 .8rem
        }

        .result{
            background:$white;
            width:100%;
            left:50%;
            transform:translateX(-50%);
            flex-direction: column;
            align-items: flex-start;
            padding:1.2rem;
            font-size: 1.1rem;
            font-weight: 700;
            border-radius: .4rem;

            p{
                width:100%;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                margin-bottom:.6rem;
            }
            hr{
                width:100%;
            }
            .link-button-container{
                flex-direction: column;
                width:100%;
                input{
                    font-size: 1.1rem;
                    width:100%;
                    outline:none;
                    border:none;
                    color:$cta;
                    margin:.6rem 0;
                    background:none;
                }
                a{
                    width:100%;
                    text-align: center;
                    cursor:pointer;
                }
            }
        }
    }
}

@media(min-width: 1100px){
    .whole-last-link-container{
        .last-link-container{
            .result{
            flex-direction: row;
            align-items: center;

            p{
                margin-bottom: 0rem;
            }

            hr{
                display:none;
            }
            .link-button-container{
                flex-direction: row;

                input{
                    right:-3rem;
                }
                a{
                    width:fit-content;
                }
            }
        }
        }
    }
}
</style>