<template>
    <div class="whole-link-container relative">
        <div class="link-container flex">
            <div class="form-container flex relative" id="form">
                <img src="../assets/bgs/bg-shorten-mobile.svg" class="absolute">
                <div class="input-container">
                    <input type="text" placeholder="Shorten a link here...">
                    <em>Please add a valid link</em>
                </div>
                <a href="" class="square" @click="shortenLink">Shorten it!</a>
            </div>
        </div>
        <div class="result flex relative">
            <p class="userLink" v-html="userLink"></p>
            <hr>
            <div class="link-button-container relative flex">
                <input type="text" class="relative shortLink" :value="shortenedLink" readonly/>
                <a class="square copyBtn" @click="copyLink" v-html="copyBtn"></a>
            </div>
        </div>
        <lastLink v-show="this.localStorage.length > 1" :key="resetComponentOfLocalStorage"/>
        <div class="statistics flex relative">
            <div class="text-container">
                <h2>Advanced Statistics</h2>
                <p class="relative">Track how your links are performing across the web with our advanced statistics dashboard.</p>
            </div>
            <div class="cards-container flex">
                <div class="line absolute"></div>
                <div class="card relative" v-for="(card, index) in cards" :key="index">
                    <div class="icon relative">
                        <img :src="card.icon" alt="">
                    </div>
                    <h3 v-html="card.title"></h3>
                    <p v-html="card.text"></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios"
import lastLink from '../components/subComponents/lastLink.vue'

export default {
    name:'Form',
    data(){
        return{
            cards:[
                {
                    icon:require('../assets/icons/icon-brand-recognition.svg'),
                    title:'Brand Recognition',
                    text:"Boost your brand recognition with each flick. Generic links don't mean a thing. Branded links help instil confidence in your content.", 
                },
                {
                    icon:require('../assets/icons/icon-detailed-records.svg'),
                    title:'Detailed Records',
                    text:"Gain insights into who is clicking your links. Knowing when and where people engage with your content helps inform better decisions.", 
                },
                {
                    icon:require('../assets/icons/icon-fully-customizable.svg'),
                    title:'Fully Customizable',
                    text:"Improve brand awareness and content discoverability through customizable link, supercharging audience engagement.", 
                }
            ],
            userLink:false,
            shortenedLink:false,
            copyBtn:'',
            resetComponentOfLocalStorage:1,
            localStorage:localStorage,
        }
    },
    watch: {
        userLink: function () {
            localStorage.setItem('userLink', this.userLink)
        },
        shortenedLink: function () {
            localStorage.setItem('shortLink', this.shortenedLink)
        }
    },
    methods:{
        shortenLink(e){
            e.preventDefault();

            let input = document.querySelector('.input-container input')
            let errMsg = document.querySelector('.input-container em')
            let result = document.querySelector('.result')
            let warning = document.querySelector('.whole-warning-container')
            let loader = document.querySelector('.whole-loader-container')
            let copyBtn = document.querySelector('.copyBtn')
            
            //error por si el input esta vacio
            if(input.value.length === 0){
                errMsg.style.display = 'block'
                input.style.border = '2px solid #F46262'
            } else {
                errMsg.style.display = 'none'
                input.style.border = '1px solid #FFFFFF'
            }

            //mostrar y cerrar loader 
            loader.style.display = 'block'
            // setTimeout(function(){loader.style.display = 'none'}, 5000)

            //asignar link original
            this.userLink = input.value
            
            //reinciar mensaje de boton de copiar
            this.copyBtn = 'Copy'
            copyBtn.style.background = '#2BD1D1'


            axios
                .get(`https://api.shrtco.de/v2/shorten?url=${this.userLink}`)
                .then(response => {
                    console.log(response)
                    //asignar el link shorteado
                    this.shortenedLink = response.data.result.full_short_link


                    if(response.request.readyState === 4){
                        loader.style.display = 'none'
                        //mostrar resultado
                        result.style.display = 'flex'  
                    }

                    //guardar link corto en local storage
                    localStorage.setItem('shortLink', this.shortenedLink)
        
                    //reiniciar componente
                    this.resetComponentOfLocalStorage += 1
                })
                            
                .catch(err => {
                    //no mostrar resultado
                    result.style.display = 'none'
                    //que salga error si hay algo escrito
                    input.value.length > 0 ? warning.style.display = 'block' : warning.style.display = 'none';
                    //cerrar loader
                    loader.style.display = 'none'

                    //eliminar localstorage
                    localStorage.clear()
                    //reiniciar componente
                    this.resetComponentOfLocalStorage -= 1
                })
        },
        copyLink(){
            //copiar el link al portapapepels
            let linkToCopy = document.querySelector(".shortLink");
            let copyBtn = document.querySelector('.copyBtn')

            linkToCopy.setAttribute('type', 'text')
            linkToCopy.select()
            const copiar = document.execCommand('copy');  

            //estilear bot√≥n de copiar
            copyBtn.style.background = '#3A3053'
            this.copyBtn = 'Copied!'
        },
    },
    components:{
        lastLink
    }

}
</script>

<style scoped lang="scss">
@import '../variables/variables.scss';

.whole-link-container{
    background:$lightGrey;

    .link-container{
        margin-bottom:-3rem;
        .form-container{
            background:$violet;
            flex-direction: column;
            padding:1.3rem;
            border-radius: .4rem;
            left:50%;
            transform:translate(-50%, -50%);
            width:85%;
            max-width: 55rem;
            font-size: 1.1rem;

            img{
                z-index: $back;
                top:0;
                right:0;
            }

           .input-container{
                width:100%;
                input{
                    border:1px solid $white;
                    outline:none;
                    padding: .5rem .5rem .5rem 1.5rem;
                    border-radius: .4rem;
                    width:100%;
                    font-size: 1rem;
                }
                em{ 
                    margin-top: .4rem;
                    color:$error;
                    font-size: .9rem;
                    display:none;
                }
           }
            a{
                width:100%;
                margin-top: 1rem;
                text-align: center;
                font-weight: 700;
            }
        }
    }

    .result{
        background:$white;
        width:85%;
        max-width: 55rem;
        left:50%;
        transform:translateX(-50%);
        flex-direction: column;
        align-items: flex-start;
        padding:1.2rem;
        font-size: 1.1rem;
        font-weight: 700;
        border-radius: .4rem;
        display:none;

        p{
            width:100%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            margin-bottom:.6rem;
        }
        hr{
            width:100%;
        }
        .link-button-container{
            flex-direction: column;
            width:100%;
            input{
                font-size: 1.1rem;
                width:100%;
                outline:none;
                border:none;
                color:$cta;
                margin:.6rem 0;
                background:none;
            }
            a{
                width:100%;
                text-align: center;
                cursor:pointer;
            }
        }
        
    }
    .statistics{
        padding:1.3rem;
        color:$primaryFont;
        text-align: center;    
        flex-direction: column;

        .text-container{
            margin-bottom: 2rem;
            h2{
                margin: 3rem 0 1rem;
            }
        }

        p{
            color:$secondaryFont;
            max-width: 22rem;
        }

        .cards-container{
            flex-direction: column;
            
            .line{
                background:$cta;
                width:.5rem;
                height:60%;
                left:50%;
                transform:translate(-50%,10%);
                z-index: $normal !important;
            }  

            .card{
                background:$white;
                margin:5rem 0;
                border-radius: .4rem;
                padding:1.3rem;
                z-index: $fixed !important;
                max-width: 22rem;

                .icon{
                    width:fit-content;
                    left:50%;
                    transform:translate(-50%, -70%);
                    margin-bottom: -2rem;
                    img{
                        border-radius: 200px;
                        padding:1rem;
                        
                    }
                }
                h3{
                    margin-bottom: 1rem;
                    font-size: 1.3rem;
                }
            }
        }

    }
}

@media(min-width: 1100px){
    .whole-link-container{
        .link-container{
             .form-container{
                 flex-direction: row;
                 align-items: flex-start;
                 padding:3.5rem 2rem;

                 .input-container{
                    width:100%;
                    input{
                        width:100%;

                    }
                }

                a{
                    width:30%;
                    margin-top: 0rem;
                    margin-left: 1.5rem;
                }
             }
        }

        .result{
            flex-direction: row;
            align-items: center;

            p{
                margin-bottom: 0rem;
            }

            hr{
                display:none;
            }
            .link-button-container{
                flex-direction: row;

                input{
                    right:-3rem;
                }
                a{
                    width:fit-content;
                }
            }
        }

        .statistics{
            .text-container{
                margin-bottom: 0rem;
                margin-top: 3rem;
                h2{
                    font-size: 2.3rem;
                }
                p{
                    left:50%;
                    transform: translateX(-50%);
                    max-width: 25rem;
                }
            }

            .cards-container{
                flex-direction: row;
                justify-content: space-evenly;
                width:75rem;
                margin-bottom: 5rem;
            
            .line{
                transform:translate(-50%,10%) rotate(90deg);
                height:70%
            }  

            .card{
                
                text-align: left;
                padding:1.3rem 2rem;

                .icon{
                    width:fit-content;
                    left:20%;
                    transform:translate(-50%, -70%);
                    margin-bottom: -2rem;

                    img{
                        border-radius: 200px;
                        padding:1rem;                        
                    }
                }
                h3{
                    margin-bottom: 1rem;
                    font-size: 1.3rem;
                }
            }

            .card:nth-child(3){
                transform:translateY(3rem)
            }
            .card:nth-child(4){
                transform:translateY(6rem)
            }
        }
        }
    }
}
</style>